.slider-box {
  width: 330px;
  overflow: hidden;
  margin-top: 32px;
  margin-left: -15px;

  @media screen and (max-width: 413px) {
    width: 80vw;
  }
  @include tablet {
    width: 428px;
    overflow: inherit;
    margin-top: -28px;
    margin-left: 0;
  }
  @include desktop {
    width: 548px;
    margin-top: -147px;
  }
  &.u-m-right {
    margin-right: -15px;
    @include tablet {
      margin-right: 0;
    }
  }
}
.swiper {
  &__img {
    background: #dadada;
    border-radius: 2px;
  }
}
.swiper.mini-slider-1 {
  margin-left: -180px;
  @include tablet {
    margin-left: auto;
  }
  .swiper-button {
    &__wraper {
      margin-left: 195px;
      @include tablet {
        margin-left: 0;
      }
    }
  }
}
.swiper.mini-slider-r {
  margin-right: -180px;
  @include tablet {
    margin-right: auto;
  }
  .swiper-button {
    &__wraper {
      margin-right: 195px;
      @include tablet {
        margin-right: 0;
      }
    }
  }
}
