.swiper-button {
  &__wraper {
    display: flex;
    margin-top: 20px;
  }
  &__prev {
    margin-right: 32px;
  }
  &__prev,
  &__next {
    display: flex;
    cursor: pointer;
    @include tablet {
      transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
      &:hover {
        fill: getColor('accent-color');
      }
    }
  }
  &__icon {
    @include box(32px);
    @media screen and (max-width: 413px) {
      width: 8vw;
    }
  }
}
.u-right {
  justify-content: flex-end;
}
.u-m-right {
  margin-left: auto;
}

//--------triple room
.triple-room {
  .slider-box {
    order: inherit;
    margin-top: 0;
    @include tablet {
      order: 1;
    }
  }
}
